html {
    height: 100%;
}

body {
  margin: 0;
  height: 100%;
  padding: 0;
  background-color: #121823;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: -webkit-flex;
  display: flex;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 40px;
  color: white;
  margin-left: 10px;
  margin-top: 20px;
}

.header img {
  width: 55px;
  height: 55px;  
  margin-right: 5px;
}

@media (max-width: 320px) {
    #root {
      width: auto;
    }
}
.buySum {
    text-align: center;
}

.confirm  {
    background-image: -webkit-linear-gradient(#0f63ff, #6399ff);
    background-image: linear-gradient(#0f63ff, #6399ff);
    border: 1px solid black;
    border-radius: 5px;
    box-shadow: 0 0 10px 1px black;
    margin-top: 30px;
    margin-bottom: auto;
    width: 210px;
    height: 40px;
    font-size: 20px;
    font-weight: 300;
    color: white;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
}

.confirm:hover {
    background-image: -webkit-linear-gradient(#6399ff, #ffffff);
    background-image: linear-gradient(#6399ff, #ffffff);
    border: 1px solid black;
    border-radius: 5px;
    box-shadow: 0 0 10px 1px rgb(255, 255, 255);
    color: #0f63ff;
    cursor: pointer;
}

.confirm:focus {
    outline: none;
}

.summaryArea {
    width: 100%;
    text-align: center;
}

.summaryAmnt {
    font-size: 30px;
}
.App {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: auto;
  margin-top: 10px;
  overflow: hidden;
}


